define("RxmAjax", ["require", "exports", "RxmAppTool", "RxmUtil"], function (require, exports, appTool, Util) {
    "use strict";
    var s = 0;
    var AuthFlag = false;
    function ajxSetting() {
        /// <field name="options" type="Object"> AJAX </field>
        /// <field name="async" type="Boolean">: true false</field>
        /// <field name="beforeSend" type="Function">    beforeSend(XHR)  Function  XMLHttpRequest  HTTP XMLHttpRequest     Ajax  false  ajax </field>
        /// <field name="cache" type="Boolean">    : truedataType  script  jsonp  false false </field>
        /// <field name="complete" type="Function"> complete(XHR, TS)       () XMLHttpRequest </field>
        /// <field name="contentType" type="String">: "application/x-www-form-urlencoded"</field>
        /// <field name="context" type="Object"> Ajax  this  this  AJAX  options  DOM  context  success  DOM </field>
        /// <field name="data" type="String">GET  URL  processData  Key/Value jQuery </field>
        /// <field name="dataFilter" type="Function"> Ajax  data  type data  Ajax type  jQuery.ajax  dataType  jQuery </field>
        /// <field name="dataType" type="String">jQuery  HTTP  MIME  XML MIME  XML 1.4 JSON  JavaScript  script :xmlhtmlscriptjsonjsonptext </field>
        /// <field name="headers" type="String"></field>
        /// <field name="error" type="Function">:  (xml  html)    XMLHttpRequest  null  "timeout", "error", "notmodified"  "parsererror"</field>
        /// <field name="global" type="Boolean"> AJAX : true false  AJAX  ajaxStart  ajaxStop  Ajax </field>
        /// <field name="ifModified" type="Boolean">: false HTTP  Last-Modified  jQuery 1.4  'etag' </field>
        /// <field name="isLocal" type="String"></field>
        /// <field name="method" type="String">The HTTP method to use for the request (e.g. POST, GET, PUT). (version added: 1.9.0)</field>
        /// <field name="mimeType" type="String">A mime type to override the XHR mime type. (version added: 1.5.1)</field>
        /// <field name="jsonp" type="String"> jsonp  "callback=?"  GET  POST  URL  "callback"  {jsonp:'onJsonPLoad'}  "onJsonPLoad=?" </field>
        /// <field name="jsonpCallback" type="String"> jsonp  jQuery  jQuery  GET   </field>
        /// <field name="password" type="String"> HTTP </field>
        /// <field name="processData" type="Boolean">: truedata() "application/x-www-form-urlencoded" DOM  false</field>
        /// <field name="scriptCharset" type="String"> dataType  "jsonp"  "script" type  "GET"  charset</field>
        /// <field name="success" type="Function"></field>
        /// <field name="traditional" type="Boolean"> true jQuery.param </field>
        /// <field name="timeout" type="Number"></field>
        /// <field name="type" type="String">: "GET") ("POST"  "GET")  "GET" HTTP  PUT  DELETE </field>
        /// <field name="url" type="String">: </field>
        /// <field name="username" type="String"> HTTP </field>
        /// <field name="xhr" type="Function">     XMLHttpRequest  IE  ActiveXObject  XMLHttpRequest  XMLHttpRequest  jQuery 1.3   $.ajax() beforeSenderrordataFiltersuccesscomplete</field>
    }
    // function ajxSpical(){}
    // ajxSpical.prototype.setup = function (data, namespaces, eventHandle) {
    //    // split the arguments to necessary controll arguements
    //    return true;
    // };
    // ajxSpical.prototype.add = function (event_) {
    //    return false;
    // };
    // ajxSpical.prototype.remove = function (event_) {
    //    // remove pseudo event: properties on $.data
    //    return false;
    // };
    // ajxSpical.prototype.teardown = function () {
    // };
    // $.event.special['cssRequestEvt'] = new ajxSpical();
    var SessionOutFlag = false;
    function ormAjax(setting) {
        var rxmret = {};
        /// <summary></summary>
        /// <param  name="setting" type="ajxSetting"> </param>
        var uid = Math.round(Math.random() * 10000000) + '';
        setting.uid = uid;
        var requestDetachEvnt = function (e, d) {
            var ajxOpt = e.data;
            if (ajxOpt.uid + '' === d.uniqueId) {
                var xhr = {};
                xhr.responseJSON = { status: '400' };
                xhr.status = 400;
                xhr.responseText = d.data || '';
                xhr.statusText = 'FAIL';
                var xhrcode = 'fail';
                var successCode = '200' || ajxOpt.successCode;
                if (d.status === '200') {
                    var j = ajxOpt.dataType || 'json';
                    var data = d.data;
                    if (j === 'json') {
                        // eval("(" + d.data + ")")
                        data = JSON.parse(d.data);
                        xhr.responseJSON = data;
                        var code = '' + (data.status || data.code || '200');
                        // 
                        // 
                        switch (code) {
                            case successCode:
                                if (ajxOpt.success) {
                                    data = typeof (data.model) !== 'undefined' ? data.model : data;
                                    xhrcode = 'success';
                                    xhr.statusText = 'OK';
                                    xhr.status = 200;
                                    ajxOpt.success(data);
                                }
                                break;
                            case '401':
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                xhr.status = 401;
                                // 
                                if (!SessionOutFlag) {
                                    // 
                                    var redirectUrl = encodeURIComponent(window.location.href);
                                    alert("<a href='" + getServer() + "/resoft/login?redirectUrl=" + redirectUrl + "'></a>");
                                    Util.alert(cssOrm.default.prompt.sessionOutFlag, function () {
                                        rxm_page.logout(1);
                                    });
                                    SessionOutFlag = true;
                                    // false
                                    setTimeout(function () {
                                        SessionOutFlag = false;
                                    }, 2000);
                                }
                                break;
                            case '400':
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                // 
                                xhr.status = 400;
                                Util.alert(cssOrm.default.prompt.appError);
                                break;
                            case '403':
                                // 
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                xhr.status = 403;
                                if (!AuthFlag) {
                                    Util.alert(cssOrm.default.prompt.unauthorised);
                                    AuthFlag = true;
                                    // false
                                    setTimeout(function () {
                                        AuthFlag = false;
                                    }, 2000);
                                }
                                break;
                            case '499':
                                // ,
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                xhr.status = 499;
                                Util.alert(cssOrm.default.prompt.reLogin, function () {
                                    rxm_page.logout(1);
                                });
                                break;
                            case '599':
                                xhr.status = 599;
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                // app
                                Util.alert(cssOrm.default.prompt.restart, function () {
                                    rxm_page.logout(2);
                                });
                                break;
                            case '500':
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                xhr.status = 500;
                                if (ajxOpt.error) {
                                    ajxOpt.error(null, '500');
                                }
                                else {
                                    Util.alert('500:' + cssOrm.default.prompt.serverError);
                                }
                                break;
                            default:
                                // if (ajxOpt.errorStatus) {
                                //   var ret = ajxOpt.errorStatus(code)
                                //   if (!ret) return
                                // }
                                if (ajxOpt.error) {
                                    ajxOpt.error(null, code);
                                }
                                else if (cssOrm.default.ajaxErrorStatus) {
                                    var ret = cssOrm.default.ajaxErrorStatus(code);
                                    if (!ret)
                                        return;
                                }
                                else {
                                    Util.alert(code + ':' + cssOrm.default.prompt.serverError);
                                }
                        }
                    }
                    else {
                    }
                }
                else {
                    if (ajxOpt.error) {
                        ajxOpt.error(null, d.status + '');
                    }
                    else {
                        rxm_page.tip(cssOrm.default.prompt.noNetwork, 'center');
                        // Util.alert(cssOrm.default.prompt.network);
                    }
                }
                if (ajxOpt.complete) {
                    ajxOpt.complete(xhr, xhrcode);
                }
                return false;
            }
            else {
                return true;
            }
        };
        cssOrm.on({
            msgType: 'netMsg',
            id: setting.uid,
            data: setting,
            callback: requestDetachEvnt,
            one: true
        });
        var url = setting.url;
        if (url && url.indexOf('http://') == -1) {
            var host = appTool.getUpdateUrl();
            if (host.length > 0) {
                var server = appTool.getServer();
                if (url.indexOf(server) === 0) {
                    url = url.replace(server, host);
                }
            }
        }
        ;
        var headers = '';
        if (setting.contentType == 'application/json') {
            var header = {
                "content-type": "application/json;charset=UTF-8"
            };
            headers = JSON.stringify(header);
            //if($.isPlainObject(setting.datadata)){}
        }
        ;
        var data = setting.data || null;
        if ($.isPlainObject(setting.data)) {
            data = JSON.stringify(data);
        }
        //var header = JSON.stringify(setting.headers || '')
        // cipCount.onEvent("count_ajax");
        //
        var PD_T = "RX__M=" + uid;
        if (url && url.indexOf("?") != -1) {
            url += "&" + PD_T;
        }
        else {
            url += "?" + PD_T;
        }
        //
        var timeout = 30;
        if (!!setting.timeout) {
            timeout = setting.timeout;
        }
        //
        if (!!setting.encode && !!setting.encode.isEncode && setting.encode.isEncode) {
            rxm_request.fetchData(url, uid, 1, '0', 'POST', data, headers, '', 0, timeout, 'true', setting.encode.key);
        }
        else {
            rxm_request.fetchData(url, uid, 1, '0', 'POST', data, headers, '', 0, timeout);
        }
        return rxmret;
    }
    ;
    // if(typeof  cipNetData!="undefined") cipNetData.cbRequest =ajxSetting.requestEvt;
    $.ajax = ormAjax;
    return ormAjax;
});
