                args._jump = true;
                if (url.indexOf('http') !== 0) {
                    if (appTool.isLocalLoad()) {
                        url = 'citp://' + url;
                    }
                    else {
                        url = appTool.getUpdateUrl() + url;
                    }
                }
                args && args.afterClose && cssOrm.on({ msgType: widgetMsgNames.JumpPageMsg, data: args.data, id: actionID, one: true, callback: args.afterClose });
