        //            data["chunkSize"] = this.options.chunkSize;
        //            data["fileSize"] = f.size;
        //            data['httpType'] = f.type;
        //            data['lastModifiedDate'] = f.lastModifiedDate;
        //            if (typeof f.thumbnail != "undefined") {
