        uploadComplete: 'uploadComplete',
        /*	type {String}
         :validateupload.on('error', handler)
                Q_EXCEED_NUM_LIMIT fileNumLimituploader
                Q_EXCEED_SIZE_LIMIT Q_EXCEED_SIZE_LIMITuploader
                Q_TYPE_DENIED 
       */
