define("RxmEventBase", [], function () {
    var slice = [].slice, separator = /\s+/, protos;
    // handlers.
    function findHandlers(arr, name, callback, context) {
        return $.grep(arr, function (handler) {
            return handler &&
                (!name || handler.e === name) &&
                (!callback || handler.cb === callback ||
                    handler.cb._cb === callback) &&
                (!context || handler.ctx === context);
        });
    }
    function eachEvent(events, callback, iterator) {
        // event
        $.each((events || '').split(separator), function (_, key) {
            iterator(key, callback);
        });
    }
    function triggerHanders(events, args) {
        var stoped = false, i = -1, len = events.length, handler;
        while (++i < len) {
            handler = events[i];
            if (handler.cb.apply(handler.ctx2, args) === false) {
                stoped = true;
                break;
            }
        }
        return !stoped;
    }
    protos = function () {
        this._events = [];
    };
    /**
     * 
     *
     * `callback`argumentstrigger
     * ```javascript
     * var obj = {};
     *
     * // obj
     * Mediator.installTo( obj );
     *
     * obj.on( 'testa', function( arg1, arg2 ) {
     *     console.log( arg1, arg2 ); // => 'arg1', 'arg2'
     * });
     *
     * obj.trigger( 'testa', 'arg1', 'arg2' );
     * ```
     *
     * `callback``return false``callback`
     * `trigger``false`
     *
     * `on``all`, `callback`arguments
     * `type``callback``callback`
     * ```javascript
     * obj.on( 'all', function( type, arg1, arg2 ) {
     *     console.log( type, arg1, arg2 ); // => 'testa', 'arg1', 'arg2'
     * });
     * ```
     *
     * @method on
     * @grammar on( name, callback[, context] ) => self
     * @param  {String}   name     
     * @param  {Function} callback 
     * @param  {Object}   [context]  
     * @return {self} 
     * @chainable
     * @class Mediator
     */
    protos.prototype.on = function (name, callback, context) {
        var me = this, set;
        if (!callback) {
            return this;
        }
        set = this._events;
        eachEvent(name, callback, function (name, callback) {
            var handler = { e: name };
            handler.cb = callback;
            handler.ctx = context;
            handler.ctx2 = context || me;
            handler.id = set.length;
            set.push(handler);
        });
        return this;
    },
        /**
         * handler
         * @method once
         * @grammar once( name, callback[, context] ) => self
         * @param  {String}   name     
         * @param  {Function} callback 
         * @param  {Object}   [context]  
         * @return {self} 
         * @chainable
         */
        protos.prototype.once = function (name, callback, context) {
            var me = this;
            if (!callback) {
                return me;
            }
            eachEvent(name, callback, function (name, callback) {
                var once = function () {
                    me.off(name, once);
                    return callback.apply(context || me, arguments);
                };
                once._cb = callback;
                me.on(name, once, context);
            });
            return me;
        };
    /**
     * 
     * @method off
     * @grammar off( [name[, callback[, context] ] ] ) => self
     * @param  {String}   [name]     
     * @param  {Function} [callback] 
     * @param  {Object}   [context]  
     * @return {self} 
     * @chainable
     */
    protos.prototype.off = function (name, cb, ctx) {
        var events = this._events;
        if (!events) {
            return this;
        }
        if (!name && !cb && !ctx) {
            this._events = [];
            return this;
        }
        eachEvent(name, cb, function (name, cb) {
            $.each(findHandlers(events, name, cb, ctx), function () {
                delete events[this.id];
            });
        });
        return this;
    };
    /**
     * 
     * @method trigger
     * @grammar trigger( name[, args...] ) => self
     * @param  {String}   type     
     * @param  {*} [...] 
     * @return {Boolean} handlerreturn falsefalse, true
     */
    protos.prototype.trigger = function (type) {
        var args, events, allEvents;
        if (!this._events || !type) {
            return this;
        }
        args = slice.call(arguments, 1);
        events = findHandlers(this._events, type);
        allEvents = findHandlers(this._events, 'all');
        return triggerHanders(events, args) &&
            triggerHanders(allEvents, arguments);
    };
    return protos;
});
